p {
    margin-right: 5px;
 }
 
 
 .postStyle {
    position: relative;
    width: 767px;
    height: 367px;
 
 
    margin-bottom: 15px;
 
 
    background: #233162;
 
 
    flex: content;
 
 
    border: 3px solid #eeeff5;
 
 
    box-shadow: 0px 0px 22px 9px #8fbbf7;
    border-radius: 5px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
 }
 
 
 .headerStyle {
    text-align: center;
    display: flex;
    padding-left: 5px;
    gap: 20px;
 
 
    color: white;
 
 
 }
 
 
 .headerImgStyle {
    display: flex;
    border-radius: 3px;
 }
 
 
 .contentStyle {
    margin: 3px;
 
 
    color: #eeeff5;
 
 
    background: radial-gradient(38.09% 5968.34% at 38.09% 0%, #68BDFB 22%, #D1DEFF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
 }
 
 
 .footerStyle {
    text-align:right;
    margin: 9px;
    display: flex;
 
 
    color: white;
 
 
    font-size: 9px;
 }
 